
import logo from '../logo.svg';

// import { NavLink } from "react-router-dom";
import React from 'react'



function Nav() {


    return (
        <div className="container">
            <nav className="navbar navbar-expand-lg navbar-dark mt-3" data-aos="fade-down" data-aos-delay="250">
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon" />
                </button>
                <img src={logo} style={{ width: '50px' }} />
                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                            <a className="nav-link" href="https://esttio.github.io/yourcv3/">Home <span className="sr-only">(current)</span></a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="https://esttio.github.io/yourcv3/">Features</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="https://esttio.github.io/yourcv3/">Pricing</a>
                        </li>
                    </ul>
                </div>
            </nav>




        </div>
    )
}


export default Nav;
