import React from 'react';


function Contoh() {
  return (
    <div>
      <div className="jumbotron jumbotron-fluid lax"
        data-lax-preset="none"
        data-lax-optimize="true"
        data-lax-bg-pos-x="10 40, 3000 1000">
        <div className="container" data-aos="fade-down">

          <h1 className="display-41 mx-auto">With our librarry or Yours sample</h1>
          <p className="text-center mt-4">Pick from our library of custom-crafted templates or create your own. Manage your profile assets directly within YourCv to ensure presentations look great.</p>

        </div>
      </div>
    </div>
  );
}

export default Contoh;
